.button__container button{
    width: 127px;
    height: 52px;
    background-color: #384564;
    border-radius: 8px;
    text-transform: uppercase;
    margin-top: 40px;
    color: #FFFFFF;
    border: none;
    font-family: 'Proxima';
    font-weight: 600;
    font-size: 12px;
    line-height: 16px;
}

